import React from 'react';
import '../styles/List.scss';
import MovieItem from './MovieItem.jsx';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faLightbulb } from '@fortawesome/free-solid-svg-icons';

const API = 'd885f351';


class List extends React.Component {
  state = {
    movies: [],
    filterText: '', 
    error: ''
  }

  handleFilter = (event) => {
    const text = event.target.value;
    this.setState({ filterText: text });
  }

  handleSearch = (event) => {
    if (event.key === 'Enter') {
      // console.log(filterText);
      const url = `https://www.omdbapi.com/?s=${this.state.filterText}&apikey=${API}`;
      // console.log(url);
      fetch(url)
      .then(response => response.json())
      .then(data => {
        if (data.Search) {
          this.setState({movies: data.Search});
          console.log(this.state.movies);
        } else {
          this.setState({movies: -1 });
          this.setState({error: 'Sorry we could not find what you were looking for, try again!'});
        }
      });
       
    }
  }


  render() {
    const { movies, filterText, error, liked } = this.state;
    let movieList = '';  
    if (movies.length === 0) {
      movieList = 
      <div className="empty">
        <FontAwesomeIcon icon={faLightbulb} />
        <p className="empty__claim">Don't know what to search?</p>
        <p>Here's an offer you can't refuse</p>
      </div>; 
    } else if (movies === -1) {
      movieList = 
      <p className="error">{error}</p>
    } else {
      movieList = movies.map(movie => {
        return (
          <MovieItem
          key={movie.imdbID}
          id={movie.imdbID}
          imageUrl={movie.Poster}
          year={movie.Year}
          title={movie.Title}
          type={movie.Type}
          />
        )
      });  
    }
    return (
    <div className="container">
      <input className="search" onChange={this.handleFilter} onKeyDown={this.handleSearch} placeholder="Search Movies..."/>
      <div className="movies">
        {movieList}
      </div>
    </div>
    
    );
  }
}

export default List;
